input[type=text] 
{
  margin-top: 40px;
  padding:5px; 
  padding-left: 20px;
  background:white;
  border:2px solid blue;
  border-radius: 25px;
  width: 400px;
  height: 25px;
  font-family: 'Merriweather', serif;
}
.App-header div:hover
{
  background:aqua;
  text-shadow: 0.5px 0.5px blue;
  box-shadow: 10px 10px 9px turquoise;
}
.App-header div p details summary
{
  color:blue;
  background:white ;
  font-size: 25px;
  text-align: left;
  
}
.App-header div p details
{
  color:darkcyan;
  background:white;
  font-size: 18px;
  text-align:left;
}
